import React from 'react';
import {Link, NavLink} from 'react-router-dom'
import ReactRoundedImage from 'react-rounded-image'
import img from './usman-min.jpg';
import './Nav.css';
import {motion} from 'framer-motion';

function Nav() {
    return (
        <div className="nav">
       
            <nav>            
                <ul>
                    <motion.li
                        whileHover={{color: '#d2e4ed'}}
                        whileTap={{scale: 0.9}}
                    >
                        <NavLink activeClassName="active" to="/" exact>About</NavLink>
                    </motion.li>
                    <motion.li
                        whileHover={{color: '#d2e4ed'}}
                        whileTap={{scale: 0.9}}
                    >
                        <NavLink activeClassName="active" to="/projects">Projects</NavLink>
                    </motion.li>
                </ul>
            </nav>
        </div>
    )
}

export default Nav
