import React from 'react'
import {GithubOutlined, LinkedinFilled, MailFilled} from '@ant-design/icons';
import './Footer.css';
import {motion} from 'framer-motion';


function Footer() {
    return (
        <div className="footer">
            <motion.a href="https://github.com/umanzoor1904" 
                whileHover={{color: '#d2e4ed'}} target="_blank" rel="noopener noreferrer"><GithubOutlined style={{fontSize: '24px'}} /></motion.a>
            <motion.a href="https://www.linkedin.com/in/umanzoor/" whileHover={{color: '#d2e4ed'}} target="_blank" rel="noopener noreferrer"><LinkedinFilled style={{fontSize: '24px'}}/></motion.a>
            <motion.a href="mailto:umanzoor1904@gmail.com" whileHover={{color: '#d2e4ed'}}><MailFilled style={{fontSize: '24px'}}/></motion.a>
        </div>
    )
}

export default Footer
