import React from 'react'
import './Projects.css';
import img from './Capture.JPG'
import ld from './LD.png';
import chat from './Chat.JPG'
import {motion} from 'framer-motion';

const variant = {
    initial: {
        opacity: 0,
        x: "100vw"
    },
    animate: {
        opacity: 1,
        x: 0,
    }
}

function Projects() {
    
    return (
        <motion.div className="projects"
            variants={variant}
            initial="initial"    
            animate="animate"
            exit={{x: '100vw'}}
            transition={{duration: 0.5}}
        >
            
            
            <motion.div className="projects__item"
                variants={variant}
                transition={{ type: "spring", duration:"0.5"}}
            >
                <div>
                    <img src={img}/>
                </div>
                <div className="projects__item__right">
                    <h3>Covid Tracker</h3>
                    <p>A web app made with React that tracks Covid cases from around the world.</p>
                    <div>
                        <motion.a href="https://lucid-newton-e8fb2f.netlify.app/"  target="_blank" rel="noopener noreferrer" 
                            whileHover={{color: '#d2e4ed'}}
                        >Demo</motion.a>
                        <motion.a href="https://github.com/umanzoor1904/COVID19-Tracker"  target="_blank" rel="noopener noreferrer"
                            whileHover={{color: '#d2e4ed'}}
                        >Code</motion.a>
                    </div>
                </div>
            </motion.div>
            
            <motion.div className="projects__item"
                variants={variant}
                transition={{delay: 0.3, type: "spring", duration:"0.5"}}
            >
                <div>
                    <img src={ld} width="280px"/>
                </div>
                <div className="projects__item__right">
                    <h3>laser Defence</h3>
                    <p>A classic 2D space shooting game made with Unity and C#.</p>
                    <div>
                        <motion.a href="https://simmer.io/@umanzoor/laser-defence"  target="_blank" rel="noopener noreferrer"
                            whileHover={{color: '#d2e4ed'}}
                        >Demo</motion.a>
                        <motion.a href="https://github.com/umanzoor1904/Laser-Defence"  target="_blank" rel="noopener noreferrer"
                            whileHover={{color: '#d2e4ed'}}
                        >Code</motion.a>
                    </div>
                </div>
            </motion.div>

            <motion.div className="projects__item"
                variants={variant}
                transition={{delay: 0.5, type: "spring", duration:"0.5"}}
            >
                <div>
                    <img src={chat} width="280px"/>
                </div>
                <div className="projects__item__right">
                    <h3>Chat App</h3>
                    <p>A chat app created using React and Firebase.</p>
                    <div>
                        <motion.a href="https://chatapp-react-e7a4c.web.app/"  target="_blank" rel="noopener noreferrer"
                            whileHover={{color: '#d2e4ed'}}
                        >Demo</motion.a>
                        <motion.a href="https://github.com/umanzoor1904/Chat-App-React"  target="_blank" rel="noopener noreferrer"
                            whileHover={{color: '#d2e4ed'}}
                        >Code</motion.a>
                    </div>
                </div>
            </motion.div>
        </motion.div>
    )
}

export default Projects
